.container{
    width: 80%;
    background-color: #13161b;
    padding: 2rem;
}

.titleContainer{
    padding: 0.75rem 1rem;
    display: flex;
    align-items: center;
    background-color: #0b0d0f;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    min-height: 32px;
}

.profilePic{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    margin-right: 1rem;
}

.name{
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0px;
    color: white;
}

.chatContainer{
    height: calc(100% - 120px);
    overflow-y: scroll;
}

::-webkit-scrollbar{
    width: 5px;
}

::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0);
}

::-webkit-scrollbar-thumb {
    border-radius: 100vw;
    background-color: #2d3137;
}

.mainContainer{
    height: 100%;
    background-color: #1e232a;
    border-radius: 10px;
}

.chats{
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.inputContainer{
    padding: 0.5rem;
    display: flex;
    justify-content: space-between;
}

.input{
    height: 3rem;
    width: calc(89% - 1rem);
    border-radius: 10px;
    border: none;
    padding-left: 1rem;
    outline: none;
}

.button{
    width: 10%;
    border-radius: 10px;
    border: none;
    transition: all 0.2s ease;
}

.button:hover{
    background-color: #0b0d0f;
    color: white;
}